import { IcoMoonSelection } from '../../interfaces/interfaces';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';

/**
 * Generated class for the SelectVersionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-select-version',
  templateUrl: 'select-version.html',
})
export class SelectVersionPage {

  icoMoonSelection: IcoMoonSelection;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public viewCtrl: ViewController
  ) {
    this.icoMoonSelection = navParams.data.ico;
  }

  ionViewDidLoad() { }

}



